@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Overpass:wght@300;600&family=Sora:wght@100;300;400;500;600;700&display=swap");

$font: "Sora";
$accent-color: #59acb5;
$primary-color: #e0fcff;
$secondary-color: #3d8fa8;
$tertiary-color: #1f2833;
$gray-color: #c5c6c7;
$light-gray: #e9e9e9;
$clip-distance: 0.05;
$clip-size: 200px * (1 + $clip-distance * 2);
$path-width: 2px;

@keyframes moveOut {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(-100%);
  }
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 100%;
  }
}

@keyframes bounce {
  0%,
  40% {
    transform: translateY(0);
  }
  20% {
    transform: translateY(-20px);
  }
}

@keyframes bounce-down {
  0%,
  40% {
    transform: translateY(0);
  }
  20% {
    transform: translateY(20px);
  }
}

@keyframes clipMe {
  0%,
  100% {
    clip: rect(0px, $clip-size, $path-width, 0px);
  }
  25% {
    clip: rect(0px, $path-width, $clip-size, 0px);
  }
  50% {
    clip: rect($clip-size - $path-width, $clip-size, $clip-size, 0px);
  }
  75% {
    clip: rect(0px, $clip-size, $clip-size, $clip-size - $path-width);
  }
}

@keyframes border-top {
  0% {
    transform-origin: left;
  }

  25% {
    transform-origin: left;
    transform: scaleX(0);
  }

  26% {
    transform-origin: right;
    transform: scaleX(0);
  }

  50% {
    transform: scaleX(100%);
    transform-origin: right;
  }

  51% {
    transform-origin: left;
  }

  75% {
    transform: scaleX(50%);
  }
}

@keyframes border-left {
  0% {
    transform-origin: top;
  }

  25% {
    transform-origin: top;
    transform: scaleY(100%);
  }

  26% {
    transform-origin: bottom;
  }

  50% {
    transform-origin: bottom;
    transform: scaleY(0%);
  }
}

@keyframes border-bottom {
  0% {
    transform-origin: right;
  }

  25% {
    transform-origin: right;
    transform: scaleX(0);
  }

  26% {
    transform-origin: left;
    transform: scaleX(0);
  }

  50% {
    transform: scaleX(100%);
    transform-origin: left;
  }

  51% {
    transform-origin: right;
  }

  75% {
    transform: scaleX(50%);
  }
}

@keyframes border-right {
  0% {
    transform-origin: bottom;
  }

  25% {
    transform-origin: bottom;
    transform: scaleY(100%);
  }

  26% {
    transform-origin: top;
  }

  50% {
    transform-origin: top;
    transform: scaleY(0%);
  }

  100% {
    transform-origin: top;
  }
}

@mixin center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin columnFlex {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@mixin sectionBackground {
  background-repeat: no-repeat;
  background-attachment: fixed;
}

* {
  font-size: 16px;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

html {
  width: 100%;
  margin: 0;
}

body {
  // overflow: hidden;
  width: 100%;
  max-width: 100vw;
  font-family: $font;
  min-height: 100vh;
}

// Preloader

#preloader {
  @include center();
  background-color: $tertiary-color;
  position: fixed;
  width: 100%;
  height: 100vh;
  z-index: 99;
  transition: all 0.5s;
  transition-delay: 0.5s;

  &.inactive {
    transform: translateY(-100%);
    opacity: 0;
  }
}

.loader {
  position: relative;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  animation: rotate 2s linear infinite;
  transition: all 0.5s;

  span {
    position: absolute;
    top: 20px;
    left: 20px;
    bottom: 20px;
    right: 20px;
    background: $tertiary-color;
    border-radius: 50%;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0%;
    left: 0%;
    width: 50%;
    height: 100%;
    background: linear-gradient(
      to top,
      transparent,
      rgba($color: $accent-color, $alpha: 0.4)
    );
    background-repeat: no-repeat;
    background-size: 100px 180px;
    border-top-left-radius: 100px;
    border-bottom-left-radius: 100px;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 20px;
    height: 20px;
    background: $accent-color;
    border-radius: 50%;
    filter: brightness(150%);
    box-shadow: 0 0 20px $accent-color, 0 0 40px $accent-color,
      0 0 60px $accent-color, 0 0 80px $accent-color, 0 0 100px $accent-color;
  }
}

// Main body

#main-body {
  visibility: hidden;
}

// Navbar

.navbar {
  overflow: hidden;
  position: fixed;
  display: flex;
  flex-direction: column;
  transform: translateX(100%);
  background-color: white;
  font-weight: 700;
  font-size: 1rem;
  height: 100%;
  width: 100vw;
  top: 0;
  z-index: 5;
  padding: 1rem 2rem;
  transition: all 250ms ease-in-out;

  &.open {
    transform: translateX(0);
  }

  .nav-links {
    display: flex;
    flex-direction: column;
    gap: 2em;
    margin: auto;
  }

  .nav-link {
    padding: 1em;

    &:active {
      color: $accent-color;
      background-color: rgba($color: $accent-color, $alpha: 0.5);
    }
  }

  .nav-link__content {
    display: flex;
    gap: 1em;
  }

  .navbar-menu-icon {
    cursor: pointer;
    align-self: flex-end;
    width: 36px;
  }

  img {
    width: 24px;
  }

  a {
    display: flex;
    align-items: center;
    gap: 1em;
    color: black;
    text-decoration: none;

    &:visited {
      color: black;
    }
  }

  &.sticky {
    background-color: rgba($color: white, $alpha: 0.8);
  }

  .website-icon {
    display: none;
  }
}

.section-title {
  @include columnFlex();
  text-align: center;
  height: 100vh;
  transition: all 0.5s ease-in-out;

  h2 {
    font-weight: 400;
    font-size: 1.5rem;
  }
}

.underline {
  width: 8rem;
  height: 0.35rem;
  margin-top: 1rem;
  margin-bottom: 1.25rem;
  background: $accent-color;
  margin-left: auto;
  margin-right: auto;
}

// Header

.header {
  position: relative;
  overflow: hidden;
  @include columnFlex();
  background-color: white;
  padding: 1rem 2rem;
  justify-content: flex-start;

  .down-icon {
    align-self: flex-end;
    animation: bounce 2s infinite;
    transition: transform 0.5s ease-in-out, filter 0.25s ease-in-out;

    &:hover {
      animation-play-state: paused;
      filter: brightness(50%);
    }
  }
}

.header-icons {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-menu-icon {
  cursor: pointer;
  align-self: flex-end;
  width: 36px;
  height: auto;
}

.website-icon {
  width: 40px;
}

.header-section-center {
  cursor: default;
  width: 100%;
  max-width: 375px;
  margin: auto 0;
  position: relative;
  padding: 1em;
  display: flex;
  flex-direction: column;
  text-align: center;

  .header-introduction {
    opacity: 0;
    transition: all 1s ease-in-out;

    &.show {
      opacity: 100%;
    }
  }

  h1 {
    font-weight: 400;
    display: inline-block;
  }

  strong {
    font-weight: 400;
    color: $accent-color;
  }

  h3 {
    opacity: 0;
    font-weight: 500;
    transition: all 1s ease-in-out;

    &.show {
      opacity: 100%;
    }
  }

  &:hover {
    .header-line {
      &.top {
        animation: border-top 2s ease;
      }

      &.left {
        animation: border-left 2s ease;
      }

      &.down {
        animation: border-bottom 2s ease;
      }

      &.right {
        animation: border-right 2s ease;
      }
    }
  }
}

.header-line {
  position: absolute;
  background: black;

  &.top,
  &.down {
    width: 100%;
    height: 5px;
    transform: scaleX(50%);
  }

  &.top,
  &.left {
    top: 0;
    left: 0;
  }

  &.left,
  &.right {
    width: 5px;
    height: 100%;
  }

  &.down,
  &.right {
    bottom: 0;
    right: 0;
  }

  &.left,
  &.right {
    transform: scaleY(0);
  }

  &.top {
    transform-origin: left;
    animation: border-top 2.5s ease;
    animation-delay: 2s;
  }

  &.left {
    animation: border-left 2.5s ease;
    animation-delay: 2s;
  }

  &.down {
    transform-origin: right;
    animation: border-bottom 2.5s ease;
    animation-delay: 2s;
  }

  &.right {
    animation: border-right 2.5s ease;
    animation-delay: 2s;
  }
}

// About

#about {
  background-color: #000000;
  color: white;

  .section-title {
    color: white;
    background-image: url("./images/account_background.png");
    background-repeat: no-repeat;
    background-position: top 50% right;
  }
}

.about-center {
  margin: 0 auto;
  padding: 4em 1.5em 10em;

  h2 {
    color: $accent-color;
    font-size: 1.125rem;
    margin-bottom: 1.25em;
  }

  p {
    font-size: 0.75rem;
    line-height: 1.8em;

    &:last-of-type {
      margin-bottom: 4em;
    }
  }
}

.skills-grid {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 1em;
  margin: 0 auto;
  padding-left: 10vw;
}

.item {
  font-size: 0.8em;
  list-style-position: inside;

  &::marker {
    color: $accent-color;
  }
}

// Experience

#experience {
  background-color: white;

  .section-title {
    background-image: url("./images/office_bag_bg.png");
    background-position: top 40% left;
    background-repeat: no-repeat;
  }
}

.experience-center {
  margin: 0 auto 75%;
  max-width: 600px;
  height: 600px;
  padding: 4em 1.5em;

  .btn-container {
    display: flex;
    justify-content: space-between;
  }

  .divider-line {
    border-top: 3px solid black;
    width: 100%;
  }

  .experience-btn {
    cursor: pointer;
    flex: 1 1 auto;
    padding: 0.5em;
    font-size: 1em;
    font-weight: 600;
    position: relative;
    outline: none;
    border: none;
    background-color: transparent;
    transition: transform 0.2s ease-in-out;

    &:focus,
    &:active {
      color: $accent-color;
    }

    &:hover {
      // color: $accent-color;
      transform: translateY(-10px);
      text-decoration: underline;
      text-decoration-color: $accent-color;
      text-decoration-thickness: 3px;
    }

    &.active-btn {
      // background-color: rgba($color: $accent-color, $alpha: 0.25);
      color: $accent-color;
    }
  }
}

.experience-info {
  padding: 2em 1em;
  transition: all 0.4s ease-in-out;

  h3 {
    color: $accent-color;
    font-weight: 600;
    letter-spacing: 0.125em;
  }

  h4 {
    font-size: 1.25em;
    margin-bottom: 0.5em;
  }

  .experience-date {
    color: #777;
    margin-bottom: 2em;
  }

  .description {
    display: flex;
    gap: 1em;
  }

  .description + .description {
    margin-top: 1em;
  }

  .job-icon {
    padding-top: 2px;
    color: $secondary-color;
  }

  .job-desc {
    line-height: 1.5rem;
  }
}

// Education

.education-center {
  max-width: 600px;
  margin: auto;
  margin-bottom: 20vh;

  h1 {
    text-align: center;
    font-size: 1.5rem;
    letter-spacing: 0.25em;
    margin-bottom: 5rem;
  }
}

.institutions {
  margin: 0 auto;
  display: grid;
  grid-template-rows: 1fr 1fr;
  gap: 5rem;
  width: clamp(250px, 80%, 300px);
  text-align: start;

  img {
    transition: all 250ms ease-in;
    width: 60px;
  }

  h2 {
    color: $accent-color;
    font-size: 1em;
    letter-spacing: 0.125em;
    margin-bottom: 0.5em;
  }

  h3 {
    font-size: 0.825em;
  }

  p {
    font-size: 0.75em;
    margin-top: 0.5em;
    color: #777;
  }
}

.college {
  display: flex;
  flex-direction: column;
  position: relative;
  height: 300px;
  overflow: hidden;
  border-radius: 0.25em;
  box-shadow: 0 0 20px rgba($color: black, $alpha: 0.7);

  &:hover {
    .thumbnail {
      transform: scale(110%);

      &::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgba($color: $secondary-color, $alpha: 0.75);
      }
    }
    .education-container {
      transform: translateY(0);
    }
  }
}

.education-container {
  margin-top: auto;
  background: white;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 0.75em;
  z-index: 2;
}

.thumbnail {
  transition: all 0.35s ease-in-out;
  position: absolute;
  width: 100%;
  height: 100%;
}

.USC {
  .thumbnail {
    background: url("./images/USC_bg.jpg") no-repeat center;
    background-size: cover;
  }
}

.ECC {
  .thumbnail {
    background: url("./images/ECC_bg.jpg") no-repeat center;
    background-size: cover;
  }
}

// Projects

#projects {
  .section-title {
    background-image: url("./images/monitor_bg.png");
    background-position: top 50% right;
    background-repeat: no-repeat;
  }

  .section-center {
    padding: 4em 1.5em;

    @include columnFlex();
    gap: 10rem;
  }
}

.project {
  @include columnFlex();
  align-items: flex-start;

  img {
    width: 275px;
    align-self: center;
    transition: transform 0.2s ease-in-out;

    &:hover {
      transform: scale(105%);
    }
  }
}

.project__content {
  padding: 1em 10px;
  max-width: 275px;

  h3 {
    color: $accent-color;
    margin-bottom: 0.5em;
  }

  p {
    margin-bottom: 2em;
    line-height: 1.5em;
  }
}

.project__tools {
  display: flex;
  flex-wrap: wrap;
  gap: 1em;
}

.tool {
  color: #c7c7c7;
  background-color: black;
  padding: 0.25em 0.5em;
  border-radius: 0.35em;
}

// Contact

#contact {
  background-color: black;
  background-image: url("./images/message_bg.png");
  background-position: top 25% left;
  background-repeat: no-repeat;

  text-align: center;
  color: white;
  gap: 15vh;

  .contact-center {
    height: 100%;
    margin: auto;
    display: flex;
    flex-direction: column;
  }

  .section-title {
    height: auto;
    color: white;
    flex: 1;
    justify-content: flex-end;
  }

  .center-container {
    padding-bottom: 2em;
    display: flex;
    flex-direction: column;
    flex: 1;

    h3 {
      font-size: 1em;
      font-weight: 400;
      margin-bottom: 2em;
    }

    a {
      color: $accent-color;

      &:visited {
        color: $accent-color;
      }
    }

    .down-icon {
      margin-right: 2rem;
      align-self: flex-end;
      animation: bounce-down 2s infinite;
      transition: transform 0.5s ease-in-out, filter 0.25s ease-in-out;
      &:hover {
        animation-play-state: paused;
        filter: brightness(150%);
      }

      img {
        transform: rotate(180deg);
        width: 60px;
      }
    }
  }

  .social-media {
    margin-top: auto;
    p {
      font-size: 0.75em;
    }
  }

  .sns-links {
    margin-top: 2em;

    a + a {
      margin-left: 2em;
    }

    img {
      width: 30px;
      transition: transform 0.2s ease-in-out;

      &:hover {
        transform: scale(110%);
        filter: invert(1) invert(67%) sepia(42%) saturate(427%)
          hue-rotate(138deg) brightness(86%) contrast(88%);
      }
    }
  }
}

.hidden {
  opacity: 0;

  &.left {
    transform: translateX(-100vw);
  }

  &.right {
    transform: translateX(100vw);
  }
}

@media screen and (min-width: 875px) {
  .section-title {
    background-size: 30% auto;
  }

  // Navbar

  .navbar {
    background: rgba($color: white, $alpha: 0.5);
    backdrop-filter: blur(5px);
    position: sticky;
    transform: translateX(0);
    height: fit-content;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    padding: 0 2rem;

    .nav-links {
      flex-direction: row;
      margin: 0;
      margin-left: auto;
      gap: 0;
    }

    .nav-link {
      padding: 1.25rem 2rem;
      padding-top: calc(1.25rem + 3px);
      transition: all 0.2s ease-in-out;
      border-bottom: 3px solid rgba($color: $accent-color, $alpha: 0);

      &:hover {
        background-color: rgba(255, 255, 255, 0.25);
        transform: translateY(-5px);
        border-bottom: 3px solid $accent-color;
      }
    }

    .nav-link__content {
      span {
        font-size: 1rem;
      }
    }

    .website-icon {
      display: block;

      img {
        width: 60px;
      }
    }
  }

  .navbar-menu-icon {
    display: none;
  }

  // Header
  .header {
    h1,
    strong {
      font-size: 2.5rem;
    }

    h3 {
      font-size: 1.25rem;
    }
  }

  .website-icon {
    display: none;
  }

  // About

  #about {
    background-image: url("./images/account_background.png");
    background-repeat: no-repeat;
    background-position: top 50% right;
    background-attachment: fixed;
    background-size: 500px;

    .section-title {
      background-image: none;
    }
  }

  .about-center {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0),
      rgba(0, 0, 0, 0.4) 20%,
      rgba(0, 0, 0, 0.8)
    );

    width: 100%;

    h2 {
      font-size: 1.5em;
    }

    p {
      font-size: 1em;
    }
  }

  .about-description {
    max-width: 600px;
  }

  .skills {
    max-width: 600px;
  }

  .skills-grid {
    padding-left: 0;
    grid-template-columns: repeat(4, 1fr);
  }

  .item {
    padding-left: 1em;
    font-size: 1em;
  }

  // Experience

  #experience {
    background-image: url("./images/office_bag_bg.png");
    background-repeat: no-repeat;
    background-position: top 50% left;
    background-attachment: fixed;
    background-size: 500px;

    .section-title {
      background-image: none;
    }

    .experience-center {
      background: linear-gradient(
        to bottom,
        rgba(255, 255, 255, 0),
        rgba(255, 255, 255, 0.5) 25%,
        rgba(255, 255, 255, 0.7)
      );
      max-width: none;
      width: 100%;
      height: 100vh;
      margin-bottom: 0;

      .experience-btn {
        font-size: 1.25rem;
      }
    }
  }

  .btn-container,
  .divider-line,
  .experience-info {
    margin: 0 auto;
    max-width: 800px;
  }

  .experience-info {
    h3 {
      font-size: 1.5rem;
    }

    h4 {
      font-size: 1.75rem;
    }

    .experience-date {
      font-size: 1.125rem;
    }

    .job-desc {
      p {
        font-size: 1.125rem;
      }
    }
  }

  // Education

  .education-center {
    background: linear-gradient(
      to bottom,
      rgba($color: white, $alpha: 0.7) 90%,
      rgba($color: white, $alpha: 1)
    );
    margin: 0 auto;
    width: 100%;
    max-width: none;
    padding-bottom: 40vh;
  }

  .institutions {
    max-width: 875px;
    display: flex;
    width: auto;
    gap: 10vw;
  }

  .college {
    width: 400px;
  }

  .education-container {
    transition: all 0.35s ease-in-out;
    height: 115px;
    transform: translateY(100%);
  }

  // Projects

  #projects {
    background-image: url("./images/monitor_bg.png");
    background-repeat: no-repeat;
    background-position: top 50% right;
    background-attachment: fixed;
    background-size: 500px;

    .section-title {
      background-image: linear-gradient(
        to bottom,
        rgba(255, 255, 255, 1),
        rgba(255, 255, 255, 0) 20%
      );
    }

    .section-center {
      background-image: linear-gradient(
        to bottom,
        rgba(255, 255, 255, 0),
        rgba(255, 255, 255, 0.5) 30%,
        rgba(255, 255, 255, 1)
      );
      width: 100%;
      padding: 10vh 50vh;
      flex-direction: row;
      align-items: flex-start;
    }
  }

  .project {
    margin: 0 auto;
