* {
  font-size: 16px;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

html {
  width: 100%;
  margin: 0;
}

body {
  width: 100%;
  max-width: 100vw;
  font-family: "Sora";
  min-height: 100vh;
}

#preloader {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1f2833;
  position: fixed;
  width: 100%;
  height: 100vh;
  z-index: 99;
  transition: all 0.5s;
  transition-delay: 0.5s;
}
#preloader.inactive {
  transform: translateY(-100%);
  opacity: 0;
}

.loader {
  position: relative;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  -webkit-animation: rotate 2s linear infinite;
          animation: rotate 2s linear infinite;
  transition: all 0.5s;
}
.loader span {
  position: absolute;
  top: 20px;
  left: 20px;
  bottom: 20px;
  right: 20px;
  background: #1f2833;
  border-radius: 50%;
}
.loader::before {
  content: "";
  position: absolute;
  top: 0%;
  left: 0%;
  width: 50%;
  height: 100%;
  background: linear-gradient(to top, transparent, rgba(89, 172, 181, 0.4));
  background-repeat: no-repeat;
  background-size: 100px 180px;
  border-top-left-radius: 100px;
  border-bottom-left-radius: 100px;
}
.loader::after {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 20px;
  height: 20px;
  background: #59acb5;
  border-radius: 50%;
  filter: brightness(150%);
  box-shadow: 0 0 20px #59acb5, 0 0 40px #59acb5, 0 0 60px #59acb5, 0 0 80px #59acb5, 0 0 100px #59acb5;
}

#main-body {
  visibility: hidden;
}

.navbar {
  overflow: hidden;
  position: fixed;
  display: flex;
  flex-direction: column;
  transform: translateX(100%);
  background-color: white;
  font-weight: 700;
  font-size: 1rem;
  height: 100%;
  width: 100vw;
  top: 0;
  z-index: 5;
  padding: 1rem 2rem;
  transition: all 250ms ease-in-out;
}
.navbar.open {
  transform: translateX(0);
}
.navbar .nav-links {
  display: flex;
  flex-direction: column;
  gap: 2em;
  margin: auto;
}
.navbar .nav-link {
  padding: 1em;
}
.navbar .nav-link:active {
  color: #59acb5;
  background-color: rgba(89, 172, 181, 0.5);
}
.navbar .nav-link__content {
  display: flex;
  gap: 1em;
}
.navbar .navbar-menu-icon {
  cursor: pointer;
  align-self: flex-end;
  width: 36px;
}
.navbar img {
  width: 24px;
}
.navbar a {
  display: flex;
  align-items: center;
  gap: 1em;
  color: black;
  text-decoration: none;
}
.navbar a:visited {
  color: black;
}
.navbar.sticky {
  background-color: rgba(255, 255, 255, 0.8);
}
.navbar .website-icon {
  display: none;
}

.section-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100vh;
  transition: all 0.5s ease-in-out;
}
.section-title h2 {
  font-weight: 400;
  font-size: 1.5rem;
}

.underline {
  width: 8rem;
  height: 0.35rem;
  margin-top: 1rem;
  margin-bottom: 1.25rem;
  background: #59acb5;
  margin-left: auto;
  margin-right: auto;
}

.header {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  padding: 1rem 2rem;
  justify-content: flex-start;
}
.header .down-icon {
  align-self: flex-end;
  -webkit-animation: bounce 2s infinite;
          animation: bounce 2s infinite;
  transition: transform 0.5s ease-in-out, filter 0.25s ease-in-out;
}
.header .down-icon:hover {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
  filter: brightness(50%);
}

.header-icons {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-menu-icon {
  cursor: pointer;
  align-self: flex-end;
  width: 36px;
  height: auto;
}

.website-icon {
  width: 40px;
}

.header-section-center {
  cursor: default;
  width: 100%;
  max-width: 375px;
  margin: auto 0;
  position: relative;
  padding: 1em;
  display: flex;
  flex-direction: column;
  text-align: center;
}
.header-section-center .header-introduction {
  opacity: 0;
  transition: all 1s ease-in-out;
}
.header-section-center .header-introduction.show {
  opacity: 100%;
}
.header-section-center h1 {
  font-weight: 400;
  display: inline-block;
}
.header-section-center strong {
  font-weight: 400;
  color: #59acb5;
}
.header-section-center h3 {
  opacity: 0;
  font-weight: 500;
  transition: all 1s ease-in-out;
}
.header-section-center h3.show {
  opacity: 100%;
}
.header-section-center:hover .header-line.top {
  -webkit-animation: border-top 2s ease;
          animation: border-top 2s ease;
}
.header-section-center:hover .header-line.left {
  -webkit-animation: border-left 2s ease;
          animation: border-left 2s ease;
}
.header-section-center:hover .header-line.down {
  -webkit-animation: border-bottom 2s ease;
          animation: border-bottom 2s ease;
}
.header-section-center:hover .header-line.right {
  -webkit-animation: border-right 2s ease;
          animation: border-right 2s ease;
}

.header-line {
  position: absolute;
  background: black;
}
.header-line.top, .header-line.down {
  width: 100%;
  height: 5px;
  transform: scaleX(50%);
}
.header-line.top, .header-line.left {
  top: 0;
  left: 0;
}
.header-line.left, .header-line.right {
  width: 5px;
  height: 100%;
}
.header-line.down, .header-line.right {
  bottom: 0;
  right: 0;
}
.header-line.left, .header-line.right {
  transform: scaleY(0);
}
.header-line.top {
  transform-origin: left;
  -webkit-animation: border-top 2.5s ease;
          animation: border-top 2.5s ease;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}
.header-line.left {
  -webkit-animation: border-left 2.5s ease;
          animation: border-left 2.5s ease;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}
.header-line.down {
  transform-origin: right;
  -webkit-animation: border-bottom 2.5s ease;
          animation: border-bottom 2.5s ease;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}
.header-line.right {
  -webkit-animation: border-right 2.5s ease;
          animation: border-right 2.5s ease;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}

#about {
  background-color: #000000;
  color: white;
}
#about .section-title {
  color: white;
  background-image: url("./images/account_background.png");
  background-repeat: no-repeat;
  background-position: top 50% right;
}

.about-center {
  margin: 0 auto;
  padding: 4em 1.5em 10em;
}
.about-center h2 {
  color: #59acb5;
  font-size: 1.125rem;
  margin-bottom: 1.25em;
}
.about-center p {
  font-size: 0.75rem;
  line-height: 1.8em;
}
.about-center p:last-of-type {
  margin-bottom: 4em;
}

.skills-grid {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 1em;
  margin: 0 auto;
  padding-left: 10vw;
}

.item {
  font-size: 0.8em;
  list-style-position: inside;
}
.item::marker {
  color: #59acb5;
}

#experience {
  background-color: white;
}
#experience .section-title {
  background-image: url("./images/office_bag_bg.png");
  background-position: top 40% left;
  background-repeat: no-repeat;
}

.experience-center {
  margin: 0 auto 75%;
  max-width: 600px;
  height: 600px;
  padding: 4em 1.5em;
}
.experience-center .btn-container {
  display: flex;
  justify-content: space-between;
}
.experience-center .divider-line {
  border-top: 3px solid black;
  width: 100%;
}
.experience-center .experience-btn {
  cursor: pointer;
  flex: 1 1 auto;
  padding: 0.5em;
  font-size: 1em;
  font-weight: 600;
  position: relative;
  outline: none;
  border: none;
  background-color: transparent;
  transition: transform 0.2s ease-in-out;
}
.experience-center .experience-btn:focus, .experience-center .experience-btn:active {
  color: #59acb5;
}
.experience-center .experience-btn:hover {
  transform: translateY(-10px);
  text-decoration: underline;
  -webkit-text-decoration-color: #59acb5;
          text-decoration-color: #59acb5;
  text-decoration-thickness: 3px;
}
.experience-center .experience-btn.active-btn {
  color: #59acb5;
}

.experience-info {
  padding: 2em 1em;
  transition: all 0.4s ease-in-out;
}
.experience-info h3 {
  color: #59acb5;
  font-weight: 600;
  letter-spacing: 0.125em;
}
.experience-info h4 {
  font-size: 1.25em;
  margin-bottom: 0.5em;
}
.experience-info .experience-date {
  color: #777;
  margin-bottom: 2em;
}
.experience-info .description {
  display: flex;
  gap: 1em;
}
.experience-info .description + .description {
  margin-top: 1em;
}
.experience-info .job-icon {
  padding-top: 2px;
  color: #3d8fa8;
}
.experience-info .job-desc {
  line-height: 1.5rem;
}

.education-center {
  max-width: 600px;
  margin: auto;
  margin-bottom: 20vh;
}
.education-center h1 {
  text-align: center;
  font-size: 1.5rem;
  letter-spacing: 0.25em;
  margin-bottom: 5rem;
}

.institutions {
  margin: 0 auto;
  display: grid;
  grid-template-rows: 1fr 1fr;
  gap: 5rem;
  width: clamp(250px, 80%, 300px);
  text-align: start;
}
.institutions img {
  transition: all 250ms ease-in;
  width: 60px;
}
.institutions h2 {
  color: #59acb5;
  font-size: 1em;
  letter-spacing: 0.125em;
  margin-bottom: 0.5em;
}
.institutions h3 {
  font-size: 0.825em;
}
.institutions p {
  font-size: 0.75em;
  margin-top: 0.5em;
  color: #777;
}

.college {
  display: flex;
  flex-direction: column;
  position: relative;
  height: 300px;
  overflow: hidden;
  border-radius: 0.25em;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.7);
