}
.college:hover .thumbnail::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(61, 143, 168, 0.75);
}
.college:hover .education-container {
  transform: translateY(0);
}

.education-container {
  margin-top: auto;
  background: white;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 0.75em;
  z-index: 2;
}

.thumbnail {
  transition: all 0.35s ease-in-out;
  position: absolute;
  width: 100%;
  height: 100%;
}

.USC .thumbnail {
  background: url("./images/USC_bg.jpg") no-repeat center;
  background-size: cover;
}

.ECC .thumbnail {
  background: url("./images/ECC_bg.jpg") no-repeat center;
  background-size: cover;
}

#projects .section-title {
  background-image: url("./images/monitor_bg.png");
  background-position: top 50% right;
  background-repeat: no-repeat;
}
#projects .section-center {
  padding: 4em 1.5em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10rem;
}

.project {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-items: flex-start;
}
.project img {
