}
#contact .sns-links img:hover {
  transform: scale(110%);
  filter: invert(1) invert(67%) sepia(42%) saturate(427%) hue-rotate(138deg) brightness(86%) contrast(88%);
}

.hidden {
  opacity: 0;
}
.hidden.left {
  transform: translateX(-100vw);
}
.hidden.right {
  transform: translateX(100vw);
}

