  .section-title {
    background-size: 30% auto;
  }

  .navbar {
    background: rgba(255, 255, 255, 0.5);
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    position: -webkit-sticky;
    position: sticky;
    transform: translateX(0);
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    padding: 0 2rem;
  }
  .navbar .nav-links {
    flex-direction: row;
    margin: 0;
    margin-left: auto;
    gap: 0;
  }
  .navbar .nav-link {
    padding: 1.25rem 2rem;
    padding-top: calc(1.25rem + 3px);
    transition: all 0.2s ease-in-out;
    border-bottom: 3px solid rgba(89, 172, 181, 0);
  }
  .navbar .nav-link:hover {
    background-color: rgba(255, 255, 255, 0.25);
    transform: translateY(-5px);
    border-bottom: 3px solid #59acb5;
  }
  .navbar .nav-link__content span {
    font-size: 1rem;
  }
  .navbar .website-icon {
    display: block;
  }
  .navbar .website-icon img {
    width: 60px;
  }

  .navbar-menu-icon {
    display: none;
  }

  .header h1,
.header strong {
    font-size: 2.5rem;
  }
  .header h3 {
    font-size: 1.25rem;
  }

  .website-icon {
    display: none;
  }

  #about {
    background-image: url("./images/account_background.png");
    background-repeat: no-repeat;
    background-position: top 50% right;
    background-attachment: fixed;
    background-size: 500px;
  }
  #about .section-title {
    background-image: none;
  }

  .about-center {
